.mypage {
  display: flex;
  min-height: inherit;
  .main {
    display: flex;
  }
  .content-wrap {
    margin: 0 auto;
    width: 60%;
    .nav {
      display: flex;
      justify-content: space-around;
      margin-top: 5%;
      margin-bottom: 5%;
      border-radius: 10px;
      border: 1px solid #ccc;
      li {
        width: 150px;
        text-align: center;
        font-size: 17px;

        button {
          width: 85%;
          background-color: white;
          padding: 20px;
          font-weight: 700;

          &:hover {
            color: #ff0000;
            border-bottom: 3px solid #ff0000;
          }
        }
        .select {
          color: #ff0000;
          border-bottom: 3px solid #ff0000;
        }
      }
    }
  }
}